<style lang="scss">@import "./css/style.scss";</style>
<template>
    <div id="ad">
        <ToolBar>
            <div>
                <el-button type="primary" icon="el-icon-plus" size="small">新增广告</el-button>
                <el-popover
                    style="margin-left: 10px"
                    title="设置"
                    width="260"
                    trigger="hover">
                    <div>
                        <p style="margin-bottom: 10px">设置文章间隔多少插入一条广告</p>
                        <div style="display: flex;justify-content: space-between">
                            <el-input-number v-model="num"  :min="1" :max="20" label="条" size="small"></el-input-number>
                            <el-button type="primary" size="small">保存</el-button>
                        </div>

                    </div>
                    <el-button type="info" slot="reference" icon="el-icon-s-tools" size="small"></el-button>
                </el-popover>
            </div>
            <div>
                <el-input
                        placeholder="请输入广告标题"
                        size="small"
                        style="width: 140px"
                        v-model="searchParams.postTitle"
                        clearable>
                </el-input>
                <el-select v-model="searchParams.postType" size="small" clearable placeholder="文章导航"
                           style="width: 140px">
                    <el-option
                            v-for="(v,k) in $Cfg.postType"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>

                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="状态"
                           style="width: 120px">
                    <el-option
                            v-for="(v,k) in $Cfg.postStatus"
                            :key="k"
                            :label="v"
                            :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="线上状态"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-select v-model="searchParams.postStatus" size="small" clearable placeholder="操作人"
                           style="width: 120px">
                    <el-option
                        v-for="(v,k) in $Cfg.postStatus"
                        :key="k"
                        :label="v"
                        :value="k">
                    </el-option>
                </el-select>
                <el-button type="success"  size="small" @click="">查询</el-button>
                <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
            </div>
        </ToolBar>
        <el-table
                :data="tableData"
                border
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="ID"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="广告标题"
                    width="180">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="文章导航">
            </el-table-column>
            <el-table-column
                prop="address"
                label="链接地址">
            </el-table-column>
            <el-table-column
                prop="address"
                label="起始时间">
            </el-table-column>
            <el-table-column
                prop="address"
                label="轮播顺序">
            </el-table-column>
            <el-table-column
                prop="address"
                label="状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="线上状态">
            </el-table-column>
            <el-table-column
                prop="address"
                label="操作人">
            </el-table-column>
            <el-table-column
                prop="address"
                label="创建时间">
            </el-table-column>
            <el-table-column
                    label="操作"
                    width="210">
                <template slot-scope="scope">

                    <el-button @click="handleClick(scope.row)" type="danger" size="small"
                               >禁用</el-button>
                    <el-button @click="handleClick(scope.row)" type="success" size="small"
                                >预览</el-button>
                    <el-button @click="$router.push('/carousel/carousel_ad/ad_edit')" type="primary" size="small"
                                >修改</el-button>
                </template>
            </el-table-column>
        </el-table>
        <Pagination
            :params="searchParams"
            :requestFunc="requestFunc"
            @returnData="returnData"
        />
    </div>
</template>

<script>
    import ToolBar from '@/components/ToolBar.vue';
    import HelpHint from '@/components/HelpHint.vue';
    import Pagination from '@/components/Pagination.vue';
    import paginationMixin from './mixin/page.js'

    export default {
        mixins:[paginationMixin],
        data() {
            return {
                num:5,
                tableData: [{
                    date: '2016-05-03',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lornnque unde.'
                }, {
                    date: '2016-05-02',
                    name: 'Lorem ipsum dolor sit amet,',
                    address: 'Lorem  qua runde.'
                }]
            }
        },
        methods: {
            handleClick(row) {
                this.$alert(row, '标题名称', {
                    confirmButtonText: '确定',
                    callback: action => {}
                });
            },
        },
        created() {

        },
        components: {
            ToolBar, HelpHint, Pagination
        }
    }
</script>
